import React, {useState, } from 'react';
import {Button, Col, Card, Form} from 'react-bootstrap';

const TodoCard = ({editTodo, todo}) => {
    // const [id, setId] = useState(todo.id);
    const [text,setText] = useState(todo.text);
    const [disable,setDisable] = useState(true);
    const [editable,setEditable] = useState('Edit');

    const handleTextChange = (e) => {
        setText(e.target.value);
    }

    const handleButtonClick = () => {
        setDisable(!disable);
        let current = disable ? 'Done' : 'Edit';
        setEditable(current);
        editTodo(todo.id, text);
    }

    return(
        <Col>
            <Card style={{ width: '17rem',
            marginTop:"2%", marginBottom:'8%'}} >
                <Card.Body>
                    <Card.Title>
                        <input type='checkbox' value={todo.completed}></input>

                        ID:{todo.id}
                        <hr />
                        <Form.Control as='textarea' onChange={handleTextChange} 
                        plaintext={disable} readOnly={disable} value={text} />
                    </Card.Title>

                    <Button variant="info" onClick={handleButtonClick}>
                        {editable}
                    </Button>
                </Card.Body>
            </Card>
        </Col>    
    )
}

export default TodoCard;