import React from 'react'
import { Button, Col, Row, } from 'react-bootstrap'
import {Link, } from 'react-router-dom'

const Header = () => {
    return (
        <Row style={{marginTop:'5%',marginBottom:'3%'}}>
                <HeaderButton content='About' />
                <HeaderButton content='LogIn' />
                <HeaderButton content='TodoList' />
        </Row>
    )
}

const HeaderButton = ({content}) => {
    return(
        <Col>
            <Link to={content} >
                <Button variant='outline' size='lg' block>
                    {content}
                </Button>
            </Link>
        </Col>
    )
}

export default Header;