import React, {useState, } from 'react'
import {Button, Form, Col, Row} from 'react-bootstrap'

const AddTodo = ({addTodo, resetTodo}) =>{
    const [text,setText] = useState("")
    const handleClick = (e) => {
        e.preventDefault(); 
        addTodo(text);
        setText("");
    }
    const reset = () => {
        resetTodo();
    }
    return(
        <Form >
            <Row>
                <Col xs='5' sm='8' ms='8' lg='8' xl='8'>
                    <Form.Control onChange={(e)=> setText(e.target.value) }  value={text} />
                </Col>
                <Col>
                    <Button type='submit' block onClick={handleClick}>
                        Add
                    </Button>
                </Col>
                <Col>
                    <Button variant='danger' block onClick={reset}>
                        Reset
                    </Button>
                </Col>
            </Row>
        </Form>
    )
}

export default AddTodo;