export const initialState ={
    todos: [
    ]
} 
export function TodoReducer(state, action){
    switch(action.type){
        case "ADD_TODO":
            const newTodo = {id:state.todos.length+1, text:action.text, completed:false };
            return {todos:[ ...state.todos,newTodo ]};

        case "EDIT_TODO":
            let edit_target = state.todos.find( (todo) => {
                return todo.id === action.target;
            } );
            edit_target.text = action.text;
            return {todos:[...state.todos]};

        case "COMPLETE_TODO":
            state.todos.forEach(todo => {
                if(todo.id === action.target){
                    todo.completed = action.complete;
                }
            });
            return {todos:[...state.todos]};
        case "RESET":
            return {todos:[]};
        default:
            return state;
    }
}

